.register {
  display: grid;
  place-items: center;
  color: white;
  background-color: #363a3f;
  height: 100vh;
  width: 100%;
}

.register > form {
  display: flex;
  flex-direction: column;
  background-color: #474b53;
  border-radius: 5px;
  padding: 30px;
}

.register > form > h4 {
  margin-bottom: 30px;
  text-align: center;
  font-size: 30px;
}

.register > form > label {
  color: #a5aaaf;
}

.register > form > input {
  background-color: #2f3135;
  border-radius: 3px;
  padding: 15px;
  color: white;
  border: none;
  margin: 10px 0;
}

.register > form > input:hover {
  outline-color: black;
}

.register > form > button {
  width: 400px;
  background-color: #738adb;
  color: #eff2f5;
  font-weight: 800;
  margin-top: 15px;
}

.register > form > button:hover {
  background-color: #363a3f;
  color: #738adb;
}

.return_link {
  text-align: center;
  margin-top: 20px;
  color: #a5aaaf;
}

.return_link:visited {
  color: #a5aaaf;
}

.return_link:hover {
  color: white;
}

.text-danger {
  float: right;
  color: #d44950;
}
