.chat {
  display: flex;
  flex-direction: column;
  flex: 0.75;
  background-color: #363a3f;
  height: 100vh;
}

.chat_messages {
  display: flex;
  flex-direction: column-reverse;
  flex: 1;
  overflow: scroll;
  overflow-x: hidden;
}

.chat_input {
  color: lightgray;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-radius: 5px;
  margin: 20px;
  border-top: 1px solid gray;
  background-color: #474b53;
}

.chat_input > form > input {
  padding: 15px;
  background: transparent;
  border: none;
  width: 95%;
  outline-width: 0;
  color: white;
  font-size: large;
}

.chat_input > form {
  flex: 1;
}

.chat_inputButton {
  display: none;
}

.chat_inputIcons > .MuiSvgIcon-root {
  padding: 5px;
}
