.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.25;
  height: 100vh;
  background-color: #2f3135;
}

.sidebar_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #2f3135;
  color: white;
  border-bottom: 3px solid #25282c;
}

.sidebar_channels {
  flex: 1;
  display: contents;
}

.sidebar_addChannel {
  cursor: pointer;
}

.sidebar_addChannel:hover {
  color: white;
}

.sidebar_channelsHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #2f3135;
  color: gray;
}

.sidebar_header {
  display: flex;
  align-items: center;
}

.sidebar_channelsList {
  overflow: scroll;
  overflow-x: hidden;
  height: 100%;
}

.sidebar_voice,
.sidebar_profile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: gray;
  background-color: #2f3135;
  padding: 10px;
  border-top: 1px solid gray;
}

.sidebar_voiceInfo {
  flex: 1;
  padding: 10px;
}

.sidebar_voiceIcon {
  color: #4fb185;
}

.sidebar_voiceInfo > h3 {
  color: #4fb185;
}

.sidebar_voiceInfo > p {
  font-size: smaller;
}

.sidebar_voiceIcons > .MuiSvgIcon-root {
  padding: 10px;
}

.sidebar_profileInfo {
  flex: 1;
  padding: 10px;
}

.sidebar_profileInfo > h3 {
  color: white;
}

.sidebar_profileIcons > .MuiSvgIcon-root {
  padding: 10px;
}

.sidebar_profileIcons > .MuiSvgIcon-root:last-child:hover {
  cursor: pointer;
}
